import './SignUp.css';
import { Link } from 'react-router-dom';
import application from '../../firebase';
import database from '../../firebase';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { useState } from 'react';

export const saveUser = (user) => {
  return database.child(`users/${user.uid}/info`)
    .set({
      email: user.email,
      uid: user.uid
    })
    .then(() => user)
}

function SignUp() {

  //User Authentication
  const auth = getAuth(application);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  function registration() {
    createUserWithEmailAndPassword(auth, email, password)
      .then(saveUser)
      .catch((error) => {
       const errorMessage = error.message;
       alert(errorMessage);
      })
  }

  return (
    <>
      <Navigation/>

      <main>
        <section className='middlebar'>
          <p className='warning'>
            You don't have an account yet?<br/>Create one right now!
          </p>

          <div className='component-login'>

            <nav className='leftlogin'>

              <form action={'/store.parrotcloud/login'}>
                <label>E-mail</label>
                <input type={'email'} required placeholder='Email' onChange={(e) => setEmail(e.target.value)}/>

                <label>Password</label>
                <input type={'password'} required placeholder='Password' onChange={(e) => setPassword(e.target.value)}/>

                <p>
                  by singin up you agree with the terms of privacy and our coockie police.
                </p>

                <button onClick={registration}>Create Account</button>
                <br/>
                <Link to='/login' className='terciary-button'>Login</Link>
              </form>

            </nav>
            <nav className='rigthlogin'>
              <p>
                You can signin up using
              </p>
            </nav>
          </div>
        </section>
      </main>
    </>
  );
}

const Navigation = () => {
  return (
      <header>
          <nav className="upheader">
              <span>
                  <Link to='/store.parrotcloud/' className='logo'>ParrotCloud_</Link>  
              </span>
              <span>
                  <Link to='/store.parrotcloud/login' className='login'>Sign In</Link>
              </span>
          </nav>
      </header>
  );
}

export default SignUp;